<template>
	<div class="auth_page">
		<b-container>
			<b-navbar class="app-navbar p-0">
				<b-navbar-brand to="/">
					<Logo />
				</b-navbar-brand>
			</b-navbar>
			<b-card no-body class="auth_card border-0 mx-auto">
				<h4 class="mb-5 text-center">Sign up for free</h4>
				<div class="fs-sm text-center">
					We strongly suggest <router-link to="/signup" class="text-decoration-none">signing up with your calendar provider</router-link> to sync Maw3d events
				</div>
				<b-form @submit.prevent="submit" class="mt-4">
					<b-row>
						<b-col cols="6" class="pr-2">
							<b-form-group label="First name" label-for="firstName">
								<b-form-input id="firstName" v-model="signup.firstName" v-vuelidate="signup.firstName" />
							</b-form-group>
						</b-col>
						<b-col cols="6" class="pl-2">
							<b-form-group label="Last name" label-for="lastName">
								<b-form-input id="lastName" v-model="signup.lastName" v-vuelidate="signup.lastName" />
							</b-form-group>
						</b-col>
						<b-col cols="12">
							<b-form-group label="Email" label-for="email">
								<b-form-input id="email" type="email" v-model="signup.email" v-vuelidate="signup.email" />
							</b-form-group>
						</b-col>
						<b-col>
							<b-form-group label="Password" label-for="password">
								<div class="password_wrapper">
									<b-form-input ref="password" id="password" :type="passwordType" v-model="signup.password" v-vuelidate="signup.password" />
									<span class="icon_show_password" :class="{ show: isPasswordShown }" @click.prevent="showPassword">
										<b-icon icon="eye" scale="1.2" />
									</span>
								</div>
							</b-form-group>
						</b-col>
					</b-row>
					<b-btn type="submit" variant="primary" block>Sign up for free</b-btn>
				</b-form>
				<div class="auth_card_footer fs-sm mt-4 text-center">
					<span class="text-muted">Already have an account?</span>&nbsp;
					<router-link to="/login" class="text-decoration-none">Login</router-link>
				</div>
			</b-card>
		</b-container>
	</div>
</template>

<script>
	import Logo from "@/components/layouts/Logo.vue";
	import { validationMixin } from "vuelidate";
	import { required, email, minLength, maxLength } from "vuelidate/lib/validators";
	export default {
		components: { Logo },

		mixins: [validationMixin],

		data() {
			return {
				signup: { email: "", password: "", firstName: "", lastName: "" },

				passwordType: "password",

				errors: {},
			};
		},

		validations: {
			signup: {
				email: { required, email },
				password: { required, minLength: minLength(8), maxLength: maxLength(20) },
				firstName: { required, minLength: minLength(2), maxLength: maxLength(20) },
				lastName: { required, minLength: minLength(2), maxLength: maxLength(20) },
			},
		},

		computed: {
			isPasswordShown() {
				return this.passwordType === "text";
			},
		},

		methods: {
			showPassword() {
				this.passwordType = this.passwordType === "password" ? "text" : "password";
			},

			submit() {
				this.$v.$touch();

				if (this.$v.$invalid) return;

				console.log(this.signup);
			},

			validateState(field) {
				return this.$v.signup[field].$dirty && this.$v.signup[field].$invalid;
			},
		},
	};
</script>
